import React, { Component } from 'react'
import { API_URL } from '../utils/constants'
import DaySelector from '../components/DaySelector'
import styled from 'styled-components'

const Grid = styled.div``

class SolutionForm extends Component {
	constructor(props) {
		super(props)

		console.log('API_URL: ' + API_URL)
		console.log('Git pages upload test')

		this.state = { availableDays: [] }
		fetch(API_URL + '/getDays')
			.then(response => response.text())
			.then(text => {
				try {
					const data = JSON.parse(text)
					this.setState({ availableDays: data.days })
				} catch (err) {
					console.error(err)
				}
			})
	}

	render() {
		return (
			<Grid>
				<DaySelector availableDays={this.state.availableDays} />
			</Grid>
		)
	}
}

export default SolutionForm



